.form-control{
    margin: 1rem 0;
}
.form-control label,
.form-control input,
.form-control textarea{
    display: block;
    width: 600px;
    padding: 1rem
}
.form-control label{
    font-weight: bold;
    margin-bottom: 0.5rem;
}
/* .form-control input,
.form-control textarea{
    border-color: red;
    background: #ffd1d1;
} */
.form-control input:focus,
.form-control textarea:focus{
    outline: none;
    background: #ebebeb;
    border-color: #510077;
}
.form-control--invalid label,
.form-control--invalid p{
    color: red;
}

.form-control--invalid input,
.form-control--invalid textarea{
    border-color: red;
    background: #ffd1d1;
}