.modal{
    z-index: 100;
    position: fixed;
    top: 22vh;
    left: 10%;
    width: 80%;
    background: white;
    box-shadow: 0 2px 8px rgba(0,0,0,0.26);
    border-radius: 8px;
}
.modal__header{
    width: 100%;
    padding: 0.5rem 0.5rem;
    background: #2a006e;
    color: white;
}
.modal-enter-active{
    transform: translateY(0);
    opacity: 1;
    transition: all 200ms;
}
.modal-exit{
    transform: translateY(0);
    opacity: 1;
}
.modal-exit-active{
    transform: translateY(-10rem);
    opacity: 0;
    transition: all 200ms;
}